// App
import React from "react";
import ConfigSettings from "../../config/settings";
// Elements
import Button from "../../Elements/Button";
import Input from "../../Elements/Input";
import Select from "../../Elements/Select";
import CheckBoxSwitcher from "../../Elements/CheckBoxSwitcher";
import CheckBox from "../../Elements/CheckBox";
import { connect } from "react-redux";
import { CSSTransitionGroup } from "react-transition-group";

class OfferCreate1 extends React.Component {
  state = {
    extraOptions: false,
    doContract: false,
    paymentMethods: false,
  };
  render() {
    return (
      <div className={`step-create ${this.props.className}`}>
        <div className="row">
          <div className=" col-12 col-md-8 row carType">
            <h4
              className="f-16 col-12 mb-1"
              style={{
                fontWeight: "normal",
              }}
            >
               
            </h4>

            <div>
              <Select
                options={[{ value: 4, label: "" }]}
                placeholder="  "
                onChange={(val) => {
                  if (val) this.setState({ carType: val.value });
                }}
              />
            </div>
            <div>
              <Select
                options={[{ value: 4, label: "" }]}
                placeholder="  "
                onChange={(val) => {
                  if (val) this.setState({ carType: val.value });
                }}
              />
            </div>
            <div>
              <Select
                options={[{ value: 4, label: "" }]}
                placeholder="  "
                onChange={(val) => {
                  if (val) this.setState({ carType: val.value });
                }}
              />
            </div>
            <div>
              <Select
                options={[{ value: 4, label: "" }]}
                placeholder="  "
                onChange={(val) => {
                  if (val) this.setState({ carType: val.value });
                }}
              />
            </div>
          </div>
          <div className="col-12 col-md-4 ">
            <h4
              className="f-16"
              style={{
                marginBottom: "20px",
                fontWeight: "normal",
              }}
            >
                
            </h4>
            <div className="d-inline-block pr-4 mt-2">
              <Button
                type="fill"
                className="f-17"
                paddingHorizontal="30px"
                paddingVertical="7px"
              >
                
              </Button>
            </div>
            <img
              src={ConfigSettings.defaultCar}
              style={{
                verticalAlign: "top",
                marginTop: "10px",
              }}
              alt=""
            />
          </div>
        </div>

        <div className="row swicher-wrapper">
          <div className="col-sm-6 col-md-4">
            <div className="CheckBoxSwitcher-wrapper">
              <CheckBoxSwitcher
                lableClassname="f-16"
                val={this.state.extraOptions}
                onChange={() => {
                  this.setState({
                    extraOptions: !this.state.extraOptions,
                  });
                }}
                lable=" "
              />
            </div>
            <CSSTransitionGroup
              transitionName="height-animation-item"
              transitionEnterTimeout={300}
              transitionLeaveTimeout={300}
              style={{
                display: "contents",
              }}
            >
              {this.state.extraOptions && (
                <div className="pt-2 cheked-list">
                  <div className="mt-2">
                    <CheckBox id="cargo" text=" " />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo2" text="  " />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo3" text="" />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo4" text=". " />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo5" text=" " />
                  </div>
                  <div className="mt-2 d-sm-flex">
                    <CheckBox id="cargo6" text="" />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo7" text=" " />
                  </div>
                </div>
              )}
            </CSSTransitionGroup>
          </div>
          <div className="col-sm-6 col-md-4">
            <div className="CheckBoxSwitcher-wrapper">
              <CheckBoxSwitcher
                val={this.state.doContract}
                onChange={() => {
                  this.setState({ doContract: !this.state.doContract });
                }}
                lable=" "
              />
            </div>
            <CSSTransitionGroup
              transitionName="height-animation-item"
              transitionEnterTimeout={300}
              transitionLeaveTimeout={300}
              style={{
                display: "contents",
              }}
            >
              {this.state.doContract && (
                <div className="pt-2 cheked-list">
                  <div className="mt-2">
                    <CheckBox id="cargo8" text=" " />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo9" text="" />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo10" text="" />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo11" text="" />
                  </div>
                </div>
              )}
            </CSSTransitionGroup>
          </div>
          <div className="col-sm-6 col-md-4">
            <div className="CheckBoxSwitcher-wrapper">
              <CheckBoxSwitcher
                val={this.state.paymentMethods}
                onChange={() => {
                  this.setState({
                    paymentMethods: !this.state.paymentMethods,
                  });
                }}
                lable="  "
              />
            </div>
            <CSSTransitionGroup
              transitionName="height-animation-item"
              transitionEnterTimeout={300}
              transitionLeaveTimeout={300}
              style={{
                display: "contents",
              }}
            >
              {this.state.paymentMethods && (
                <div className="pt-2 cheked-list">
                  <div className="mt-2">
                    <CheckBox id="cargo12" text="" />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo13" text="  " />
                  </div>
                  <div className="mt-2">
                    <CheckBox
                      id="cargo14"
                      text="- (     )"
                    />
                  </div>
                  <div className="mt-2">
                    <CheckBox id="cargo15" text=" " />
                  </div>
                  <div className="mt-2">
                    <Select
                      className="select175px "
                      options={[
                        { value: 0, label: " " },
                        { value: 1, label: " " },
                        { value: 2, label: " " },
                      ]}
                      placeholder=" "
                    />
                  </div>
                </div>
              )}
            </CSSTransitionGroup>
          </div>
        </div>

        <div className="row mt-3 mx-0 justify-content-end">
          <Button
            type="fill"
            className="input-action"
            paddingHorizontal="40px"
            onClick={this.props.next}
          >
            
          </Button>
        </div>
      </div>
    );
  }
}
class CreateTemplateAuto extends React.Component {
  render() {
    return (
      <>
        <div className="create-page create-order-page">
          <div className="container-fluid">
            <h2 className="title">  </h2>
            <Input
              type="text"
              placeholder=" "
              className="mb-4"
              style={{
                maxWidth: "725px",
              }}
            />
            <OfferCreate1 />
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};

export default connect(mapStateToProps)(CreateTemplateAuto);
