// App
import React from "react";

// Router
import { Link } from "react-router-dom";

let questions = [
  {
    title: "  PoGrooz ",
    url: "",
    id: 1,
    ref: React.createRef(),
  },
  {
    title: " PoGrooz ",
    url: "",
    id: 2,
    ref: React.createRef(),
  },
  {
    title: "PoGrooz    ",
    url: "",
    id: 3,
    ref: React.createRef(),
  },
  {
    title: "   ? ",
    url: "",
    id: 4,
    ref: React.createRef(),
  },
];

const scrollToAnswer = (ref) => {
  ref.current.scrollIntoView({
    behavior: "smooth",
  });
};

class About extends React.Component {
  render() {
    return (
      <div className="about-page">
        <div className="container-fluid">
          <h1>  Pogrooz</h1>

          <ul>
            {questions.map((question) => {
              return (
                <li key={question.id}>
                  <Link
                    to={""}
                    onClick={(e) => {
                      e.preventDefault();
                      scrollToAnswer(question.ref);
                    }}
                  >
                    {question.title}
                  </Link>
                </li>
              );
            })}
          </ul>

          <h3 ref={questions.find((question) => question.id === 1).ref}>
              PoGrooz
          </h3>

          <h4>  PoGrooz?</h4>

          <p>
               PoGrooz      
            .      
              ,      
               !   
                    ,
                   PoGrooz  
             PoGrooz      !
          </p>

          <hr></hr>

          <h3 ref={questions.find((question) => question.id === 2).ref}>
             PoGrooz
          </h3>

          <h4>    PoGrooz  ?</h4>

          <p>
            , !       
                .     
             PoGrooz   .
          </p>

          <hr></hr>

          <h3 ref={questions.find((question) => question.id === 3).ref}>
            PoGrooz   
          </h3>

          <h4>  ?</h4>

          <p>
               App Store  Google Play   
            .      
            ,    ,   
               .
          </p>

          <hr></hr>

          <h3 ref={questions.find((question) => question.id === 4).ref}>
              
          </h3>

          <h4>    ?</h4>

          <p>
            <span>:</span>     ,  
              .    . 
               ,   , 
            .      , 
                . 
                 - ,  
                 .<br></br>
            <span>:</span>      
              ,    .  ,
              .     ,  
            .        
               ,    .
                    
               .
          </p>
        </div>
      </div>
    );
  }
}

export default About;
